import { useState } from "react";
import style from "./DeleteAcnt.module.css";
import { useTranslation } from "react-i18next";
import { useNavigate } from "react-router";
import { Notfication } from "../../../../../../validation/Snackbar";
import { useUserDeleteAccountFromQviple } from "../../../../../../hooks/setting-api";
import QLoader from "../../../../../../Loader/QLoader";

function AccountDelete({ dashboardData, userId, isApk }) {
  const navigate = useNavigate();
  const { t } = useTranslation();
  const [passwordData, setPasswordData] = useState({
    password: "",
  });
  const [userDeleteAccountFromQviple] = useUserDeleteAccountFromQviple();
  const [disabled, setDisabled] = useState(false);
  const [notificationState, setNotificationState] = useState({
    msg: "",
    run: false,
  });
  const onDeleteAccount = () => {
    if (userId || dashboardData?.user?._id) {
      setDisabled((pre) => !pre);
      userDeleteAccountFromQviple({
        id: userId ? userId : dashboardData?.user?._id,
        valid_pass: passwordData.password,
      })
        .then((res) => {
          console.info("sdlfngjkfbkjgs", res.data);
          if (res?.data?.dynamic) {
            setNotificationState({
              msg: res.data.dynamic,
              run: true,
            });
            setDisabled((pre) => !pre);
          } else {
            setDisabled((pre) => !pre);
            localStorage.clear();
            window.location.replace("/");
          }
        })
        .catch({});
    }
  };
  return (
    <>
      <div
        className={style.settingBody}
        // style={{
        //   border: isApk ? "none" : "auto",
        // }}
      >
        <h6>{t("deleting_your_qviple_account")}</h6>
        <p>{t("deleting_your_qviple_account_note_1")}</p>
        <p>{t("deleting_your_qviple_account_note_2")}</p>

        <div className={style.inputContainer}>
          <label>{t("password")}</label>
          <input
            type="password"
            value={passwordData.password}
            minLength={8}
            onChange={(e) =>
              setPasswordData({ ...passwordData, password: e.target.value })
            }
          />
        </div>

        {passwordData.password?.length >= 8 ? (
          <button
            className={`${style.btn}`}
            type="button"
            onClick={onDeleteAccount}
          >
            {t("delete")}
          </button>
        ) : (
          <button type="button" className={`${style.btn1}`} disabled>
            {t("delete")}
          </button>
        )}
      </div>
      {notificationState.run && (
        <Notfication
          msg={notificationState.msg}
          run={notificationState.run}
          setRun={() => setNotificationState({ msg: "", run: false })}
          postiton="bottom"
          type="error"
        />
      )}
      {disabled && <QLoader />}
    </>
  );
}

export default AccountDelete;
