import GetStarted from "../GetStarted";
import style from "../Auth.module.css";
import { useState } from "react";
import LoginFormWithMobile from "../Login/LoginFormWithMobile";
import LoginFormWithUserName from "../Login/LoginFormWithUserName";
import { useLocation, useSearchParams } from "react-router-dom";
import AccountDelete from "../../../ProfileSetting/User/MainBody/Stats/SettingBody/Account/AccountDelete";
const userMobile = "USER_MOBILE";
const userName = "USER_NAME";
const DeleteAccount = () => {
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  const [state, setState] = useState("USER_MOBILE");
  const onUser = () => {
    setState(userMobile);
  };
  const onUserName = () => {
    setState(userName);
  };
  return (
    <>
      {searchParams.get("type") === "confirm" ? (
        <div className={style.auth_delete_container}>
          <AccountDelete userId={getQuery.state?.id} isApk />
        </div>
      ) : (
        <GetStarted>
          <div className={style.auth_card_wrapper}>
            <>
              {state === userMobile ? (
                <LoginFormWithMobile
                  onUserName={onUserName}
                  accessFrom="DELETED_ACCOUNT"
                />
              ) : state === userName ? (
                <LoginFormWithUserName
                  isUser
                  onUserMobile={onUser}
                  accessFrom="DELETED_ACCOUNT"
                />
              ) : null}
            </>
          </div>
        </GetStarted>
      )}
    </>
  );
};

export default DeleteAccount;
